import React from 'react'
import { NavLink } from 'react-router-dom'
import './style.css'

class Nav extends React.Component {
  render() {
    return (
      <div className='nav'>
        <div className="nav__wrapper">
          <div className="logo__wrapper">
            <NavLink to='/' className="logo">Ricardo</NavLink>
          </div>
          <NavLink exact to='/'></NavLink>
          <NavLink exact to='/study'></NavLink>
          <NavLink exact to='/life'></NavLink>
          <NavLink exact to='/about'></NavLink>
          <NavLink exact to='/message'></NavLink>
        </div>
      </div>
    )
  }
}

export default Nav


